import React from 'react'
import { Link } from 'react-router-dom'
import splitVal from './images/splitVal.png'
import bind from './images/bind.jpg'
import icebox from './images/icebox.png'
import haven from './images/haven.jpg'

function StratsDef() {
    return (
        <div>
            <div>
                <Link to="/split" >
                    <h4>Split Tactics</h4>
                    <img src={splitVal} alt="split" height="150px" />
                </Link>
            </div>
            <div>
                <Link to="/bind" >
                    <h4>Bind Tactics</h4>
                    <img src={bind} alt="bind" height="150px" />
                </Link>
            </div>
            <div>
                <Link to="/icebox" >
                <h4>IceBox Tactics</h4>
                <img src={icebox} alt="icebox" height="200px" />
                </Link>
            </div>
            <div>
                <Link to="/haven" >
                    <h4>Haven Tactics</h4>
                    <img src={haven} alt="haven" height="150px" />
                </Link>
            </div>
        </div>
    )
}

export default StratsDef
