import React from 'react'
import { Link } from 'react-router-dom'

function Navbar() {
    return (
        <div>
            <header>
                <h1>Andromeda Team</h1>
                <Link to="/team">Team</Link>
                <Link to="/strats">Strats</Link>
                <Link to="/">Home</Link>
            </header>
        </div>
    )
}

export default Navbar
