import './App.css';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'

// TODO: 
//   ATTACH IMG SIDE

import Home from './components/Home'
import Team from './components/Team'
import Navbar from './components/Navbar'
import StratsDef from './components/StratsDef'

import Split from './components/Split'
import Bind from './components/Bind'
import Icebox from './components/Icebox'
import Haven from './components/Haven'

function App() {
  return (
    <div className="App">
      <Router>
        <Navbar />
        <Switch>
          <Route path="/team" >
            <Team />
          </Route>

          <Route path="/strats">
            <StratsDef />
            
          </Route>

          <Route path="/split" >
            <Split />
          </Route>

          <Route path="/bind">
            <Bind />
          </Route>

          <Route path="/icebox" >
            <Icebox />
          </Route>
          <Route path="/haven" >
            <Haven />
          </Route>

          <Route exact path="/" >
            <Home />
          </Route>
        </Switch>
      </Router>
    </div>
  );
}

export default App;
